u,
a {
  cursor: pointer;
}
.ecl-margin-bottom {
  margin-bottom: 50px;
}
.leader-line {
  z-index: 99999;
}
// Box shadow bouton
ion-button {
  -webkit-box-shadow: 0px 4px 7px 1px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 4px 7px 1px rgba(0, 0, 0, 0.1);
  border-radius: 999px;
}
ion-popover.ecl-ionpopover {
  --background: var(--ion-color-dark);
  color: var(--ion-color-light);
  &::part(content) {
    padding: 8px 16px;
    width: max-content;
    white-space: nowrap;
    border-radius: 99px;
  }
  &::part(arrow) {
    transform: rotate(180deg);
  }
}
ion-item.ecl-modal-item {
  --background: white;
}
ion-button.ecl-ionitem_button::part(native) {
  padding: 4px 16px;
}
ion-item.ecl-modal-items {
  --background: transparent;
}
ion-list:has(app-vehicle > ion-item.ecl-modal-items) {
  background: transparent;
}

ion-button[fill='clear'],
.ecl-no-shadow {
  -webkit-box-shadow: none;
  box-shadow: none;
}

// Modal Cookies
.ecl-cookies-modal {
  z-index: 99999 !important;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  @media screen and (min-width: 768px) {
    align-items: center;
  }
  &::part(content) {
    transition: all 0.3s ease-in-out;
    height: fit-content;

    @media screen and (min-width: 768px) {
      max-width: 400px;
    }
  }
}

// Modal Congrats
.ecl-congrats-modal {
  height: 100%;
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  &--push-notifications {
    background-color: var(--ion-color-primary);
    min-width: 100%;
    margin: 0;
    padding: 0;
    &__description {
      max-width: 80%;
      margin: 0 auto;
    }
  }

  ng-lottie {
    max-width: 150px;
  }

  h1 {
    font-size: 24px;
    margin-bottom: 16px;
  }
  p {
    font-size: 16px;
    margin-bottom: 32px;
  }

  &__icon {
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;

    &--bg {
      border-radius: 50%;
      background: var(--ion-color-primary);
    }

    img {
      width: 120%;
      max-width: 120%;
      height: 120%;
    }
    ion-icon {
      font-size: 40px;
      color: white;
    }
  }

  &__buttons {
    width: 100%;
    max-width: 300px;
  }
}

//Input phone avec choix pays (auth)
.ecl-phone__container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-right: 15px;
}

// Title for drawers components
.ecl-ionitem_title {
  --background: transparent;
  margin-top: var(--margin-top);
  h2 {
    margin: 0;
  }
}

// Searchbar
.ecl-searchbar {
  height: 60px !important;
  padding-inline-start: 0 !important;
  padding-inline-end: 0 !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
  --background: white !important;
  --border-radius: 54px !important;

  &-icon {
    position: absolute;
    left: 14px;
    z-index: 2;
  }
  &-end_icon {
    position: absolute;
    right: 14px;
    z-index: 2;
  }

  .searchbar-search-icon {
    display: none;
  }

  .searchbar-clear-button {
    margin-inline-end: 10px !important;
  }
  .searchbar-input-container {
    height: 100% !important;

    .searchbar-input {
      padding-left: 50px !important;
    }
  }
}

// Base unlock button styles
.ecl-button--unlock {
  position: fixed;
  z-index: 10;
  top: -70px;
  background-color: white;
  -webkit-box-shadow: 0px 4px 7px 1px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 4px 7px 1px rgba(0, 0, 0, 0.1);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);

  // Full version
  &_full {
    @extend .ecl-button--unlock;
    right: 50%;
    transform: translateX(50%);
    border-radius: 54px;
    height: 60px;
    padding: 0 3px;
    display: flex;
    justify-content: space-around;
    align-items: center;

    &.opened-breakpoint {
      right: 16px;
      transform: translateX(50%) scaleX(0.2);
      opacity: 0;
      pointer-events: none;
    }

    &.closed-breakpoint {
      opacity: 1;
    }
    &--first {
      min-width: 120px;
    }
  }

  // Minified version
  &_minified {
    @extend .ecl-button--unlock;
    width: 60px;
    height: 60px;
    right: 16px;

    &.closed-breakpoint {
      transform: translateX(50%) scaleX(0.2);
      right: 50%;
      opacity: 0;
      pointer-events: none;
    }
  }
}

.ecl-close-btn {
  position: absolute;
  top: 0;
  right: 0;
}
// Dring dring
.ecl-dring {
  position: absolute;
  padding: 20px;
  width: auto;
  height: auto;
  background-color: #5656d9;
  color: #fff;
  border-radius: 20px;
  transition: opacity 0.2s, transform 0.2s, display 0.2s allow-discrete;
  transform: translateY(3rem);
  opacity: 0;
  display: none;
  z-index: 99999;

  &__centered {
    top: 15vh;
    left: 15vw;
    max-width: 70vw;
  }

  &__tripStarted {
    top: -15vh;
    left: 20vw;
    max-width: 60vw;
  }

  &__favorites {
    background-color: #5656d9;
    padding: 10;
    position: relative;
  }

  &-open {
    display: block;
    opacity: 1;
    transform: none;

    @starting-style {
      & {
        opacity: 0;
        transform: translateY(-1rem);
      }
    }
  }
}

// Allow overflow for the Renting button to be visible
.ecl-main-drawer-modal::part(content) {
  overflow: visible;
  --overflow: visible;
  border-radius: 27px;
}
.ecl-main-drawer-modal {
  --height: 50%;
  &::part(scroll),
  ion-content,
  ion-content::part(background) {
    background-color: white;
    border-radius: 27px 27px 0 0;
  }

  .ecl-main-drawer-modal__handlebackground {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 37px;
    border-radius: 27px 27px 0 0;
    background-color: transparent; /* For browsers that do not support gradients */
    background-image: linear-gradient(white, rgba(255, 255, 255, 0));
  }
}

.ecl-whatsapp {
  --background: #285c54;
}

.myMarker {
  background-image: url('/assets/svg/markers/mymarker.svg');
  background-size: cover;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  cursor: pointer;
}

.myMarker__ripple:before,
.myMarker__ripple:after {
  position: absolute;
  content: '';
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: solid 3px #384fd9;
  border-radius: 25px;
  opacity: 0.7;
}

.myMarker__ripple:before {
  animation: ripple 3.5s linear infinite;
}

.myMarker__ripple:after {
  animation: ripple 3.5s 1s linear infinite;
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

.ecl-padding-start {
  padding-left: 5px;
}
li {
  list-style: none;
}

// Add a new color grey
:root {
  --ion-color-grey: #e8e8e8;
  --ion-color-grey-rgb: 232, 232, 232;
  --ion-color-grey-contrast: #000000;
  --ion-color-grey-contrast-rgb: 0, 0, 0;
  --ion-color-grey-shade: #cccccc;
  --ion-color-grey-tint: #eaeaea;
}

.ion-color-grey {
  --ion-color-base: var(--ion-color-grey);
  --ion-color-base-rgb: var(--ion-color-grey-rgb);
  --ion-color-contrast: var(--ion-color-grey-contrast);
  --ion-color-contrast-rgb: var(--ion-color-grey-contrast-rgb);
  --ion-color-shade: var(--ion-color-grey-shade);
  --ion-color-tint: var(--ion-color-grey-tint);
}

.ecl-btn--badge {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    top: -2px;
    right: -2px;
    z-index: 3;
    width: 12px;
    height: 12px;
    border-radius: 50%;
  }

  // tats
  &.state-error::after {
    background-color: var(--ion-color-danger);
  }

  &.state-searching::after {
    background-color: var(--ion-color-primary);
    animation: pulse 1s infinite;
  }

  &.state-pending::after {
    background-color: var(--ion-color-warning);
  }

  &.state-active::after {
    background-color: var(--ion-color-success);
  }
}

// OTP Input styles
.ecl-otp {
  &-container {
    position: relative;
    width: 100%;
    margin: 20px 0;
  }

  &-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 1;
    cursor: default;
  }

  &-slot {
    position: relative;
    width: 40px;
    height: 56px;
    border: 2px solid var(--ion-color-medium);
    border-radius: 8px;
    display: flex;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
    z-index: 2;

    &.active {
      border-color: var(--ion-color-primary);
    }
  }

  &-caret {
    position: absolute;
    width: 2px;
    height: 32px;
    background-color: var(--ion-color-primary);
    animation: blink 1s infinite;
  }
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
