/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */
/* Custom personnal styles */
@use './theme/ecovelo.scss';
/* Core CSS required for Ionic components to work properly */
@import '@ionic/angular/css/core.css';

/* Basic CSS for apps built with Ionic */
@import '@ionic/angular/css/normalize.css';
@import '@ionic/angular/css/structure.css';
@import '@ionic/angular/css/typography.css';
@import '@ionic/angular/css/display.css';

/* Optional CSS utils that can be commented out */
@import '@ionic/angular/css/padding.css';
@import '@ionic/angular/css/float-elements.css';
@import '@ionic/angular/css/text-alignment.css';
@import '@ionic/angular/css/text-transformation.css';
@import '@ionic/angular/css/flex-utils.css';

/**
 * Ionic Dark Mode
 * -----------------------------------------------------
 * For more info, please see:
 * https://ionicframework.com/docs/theming/dark-mode
 */

/* @import "@ionic/angular/css/palettes/dark.always.css"; */
/* @import "@ionic/angular/css/palettes/dark.class.css"; */
// @import '@ionic/angular/css/palettes/dark.system.css';

/* Mapbox styles */
@import 'mapbox-gl/dist/mapbox-gl.css';

@for $i from 0 through 9 {
  .marker#{$i} {
    mask-image: url('assets/svg/markers/marker.svg');
    mask-size: cover;
    -webkit-mask-image: url('assets/svg/markers/marker.svg');
    -webkit-mask-size: cover;
    background: var(--ion-color-primary);
    width: 36px;
    height: 45px;
    cursor: pointer;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 36px;
      height: 45px;
      background: white;
      mask-image: url('assets/svg/markers/marker'+#{$i}+'.svg');
      -webkit-mask-image: url('assets/svg/markers/marker'+#{$i}+'.svg');
      mask-size: cover;
      -webkit-mask-size: cover;
      z-index: -1;
    }
  }
}

#Vector {
  color: red;
}

.modal-wrapper {
  overflow: visible;
  --overflow: visible;
}

ion-modal.dialog-modal {
  --width: fit-content;
  --min-width: 250px;
  --max-width: 80%;
  --height: fit-content;
  --border-radius: 6px;
  --box-shadow: 0 28px 48px rgba(0, 0, 0, 0.4);
}

.text-danger {
  color: var(--ion-color-danger) !important;
}

.pacifico-regular {
  font-family: 'Pacifico', cursive;
  font-weight: 400;
  font-style: normal;
}
.pitstop-popover {
  --background: var(--ion-color-dark);
  &::part(content) {
    padding: 8px 16px;
    width: max-content;
    white-space: nowrap;
    border-radius: 99px;
  }
}

.pitstop-popover ion-content {
  --overflow: hidden;
  --background: var(--ion-color-dark);
  --color: rgb(255, 255, 255);
}

// Classes utilitaires pour grer les safe areas Android
.android-safe-top {
  padding-top: max(var(--ion-safe-area-top), 24px) !important;
}

.android-safe-margin-top {
  margin-top: max(var(--ion-safe-area-top), 24px) !important;
}

// Fix pour les appareils Android avec notch
@supports (padding-top: env(safe-area-inset-top)) {
  .safe-area-fix {
    padding-top: env(safe-area-inset-top, 24px);
  }
}

// Override spcifique pour Android
.platform-android {
  .custom-searchbar {
    top: calc(max(var(--ion-safe-area-top), 28px) + 16px);
  }
}
